import React, { useState,useEffect } from 'react';
import './style.css';
const getLoacatData =()=>{
    const lists=localStorage.getItem("mytodolist");
    if(lists){
        return JSON.parse(lists);

    }else{
        return [];
    }
}

const Todo = () => {
    const [inputdata, setData] = useState(" ");
    const [item, setItem] = useState(getLoacatData());
    const [isEditeItem,setIsEditItem]=useState("");
     const [togglebtn, settogglebtn]=useState(false);

    // add the item
    const addItem = () => {
        if (!inputdata) {
            alert("pls fill the data");
        }
        else if(inputdata && togglebtn){
          setItem(  item.map((curElem)=>{
                if(curElem.id ===isEditeItem){
                    return { ...curElem,name:inputdata};
                }
                return curElem;
            })
          );
          setData([]);
          setIsEditItem(null);
          settogglebtn(false);

        } else {
            const myNewInputData ={
                 id:new Date().getTime().toString(),
                name:inputdata,
                };
            
            setItem([...item,myNewInputData]);
            setData("");
            
    }
    };
    // how to delete item 
    const deleteItem = (index) => {
        const updatatedItem = item.filter((curElem) => {
            return curElem.id !== index;

        });
        setItem(updatatedItem);
    }
    // delete All elements

    const removeAll=()=>{
setItem([]);
    }

    // edit the item
 const editItem =(index)=>{
     const item_todo_edited =item.find((curElem)=>{
return curElem.id===index;

     });
     setData(item_todo_edited.name);
     setIsEditItem(index);
     settogglebtn(true);

 };


    // adding localStorage

    useEffect(() => {
    localStorage.setItem("mytodolist",JSON.stringify(item));
    }, [item])

    return (
        <>
            <div className="main-div">


                <div className="child-div">
                    <figure>
                        <img src="./images/todo.svg" alt="todo-logo" />
                        <figcaption>Add your List here 👌 </figcaption>
                    </figure>
                    <div className="AddItems">
                        <input type="text" placeholder="✍ Add Items" className="form-contorl "
                            value={inputdata}
                            onChange={(event) => setData(event.target.value)} />
                       
                         {
                             togglebtn ? ( <i className="fa fa-edit add-btn" onClick={addItem}></i> )
                             : ( <i className="fa fa-plus add-btn" onClick={addItem}></i> )
                         }
                         
                    </div>
                    {/* show our item */}

                    <div className="showItems">
                        {
                            item.map((curElem, index) => {
                                return (

                                    <div className="eachItem" key={curElem.id}>
                                        <h3>{curElem.name}</h3>
                                        <div className="todo-btn">
                                            <i className="far fa-edit add-btn" onClick={ ()=>editItem(curElem.id)}></i>
                                            <button className="far fa-trash-alt add-btn" onClick={() => deleteItem(curElem.id)}></button>
                                        </div>

                                    </div>
                                );
                            })
                        }

                    </div>
                    <div className="showItems">
                        <button className="btn effect04 " data-sm-link-text="Remove All" 
                        onClick={removeAll} >
                            <span>CHECK LIST</span>
                        </button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Todo;